ul.carList li{
	overflow:hidden; background-color:#FFF; font-size:1.3em; border-bottom:1px solid #eaeaea;
}
ul.carList li.bgc{
	background-color:#f6f6f6;
}
.c_img{
	width:118px; height:118px; overflow:hidden; border:1px solid #eaeaea;
}
ul.carList li td{
	text-align:left;
}
.c_close{
	width:26px; height:26px; position:absolute; right:0; top:2%;
}
ul.carList li td font{
	color:#ff4e00; font-size:1.4em;
}

.shopcar{max-height: 41.3rem;overflow:auto}
.add_t{
	height:5px; line-height:5px; overflow:hidden; background:url(/image/a_line.gif) no-repeat center top;
}
.a_tab td{
	padding:1% 2%; font-size:1.2em;
}
.a_t{
	width:90%; height:30px; line-height:30px; overflow:hidden; background:url(/image/h_t.gif) no-repeat left center; font-size:1.3em; padding:0 3%; margin-top:4%;
}

.buy_c{
	overflow:hidden; background-color:#FFF; margin-top:2%; border-top:1px solid #eaeaea; border-bottom:1px solid #eaeaea;
}
.pay_tab td{
	padding:2% 1%; font-size:1.3em;
}
.p_t_txt{
	width:100%; height:25px; line-height:25px; background:url(/image/ds.gif) no-repeat right center;
}

.p_txt{
	width:95%; height:60px; line-height:20px; overflow:hidden; background-color:#f6f6f6; color:#888888; font-family:"Microsoft YaHei"; padding:2%; font-size:1em; margin-left:0; border:1px solid #eaeaea;
}

.suc_tab td{
	padding:2%; text-align:center; font-size:1.3em;
}
.suc_tab td font{
	color:#ff4e00;
}
.btn_u{
	width:220px; height:35px; line-height:35px; overflow:hidden; background-color:#ff4e00; color:#FFF; font-size:1.1em; text-align:center; border:0; cursor:pointer; border-radius:2px; -mos-border-radius:2px; -webkit-border-radius:2px; margin-bottom:5%;
}
.btn_u a{
	color:#FFF;
}